<template>
	<div class="vote">
		<!--<div class="row">
			<button type="button" class="add btn btn-default">开始</button>
			<button type="button" class="add btn btn-default">停止</button>
		</div>-->
		<!--<div class="alert alert-success" data-dismiss="alert" role="alert">...</div>-->
		<div class="row">
			<div class="box" style="margin: 10px">
				<div class="btn-group btn-group-justified" role="group" aria-label="...">
					<div class="btn-group" role="group">
						<button type="button" class="btn btn-default">开始</button>
					</div>
					<div class="btn-group" role="group">
						<button type="button" class="btn btn-default">停止</button>
					</div>
				</div>
			</div>
		</div>
		<div class="row" style="text-align: center;">
			<div class="con">
				<div class="captcharBox">
					<img v-if="userId" :src="'http://mmvote.viphk.ngrok.org/api/getImg?userId=' + userId.id" :key="index">
				</div>
				<div class="tip">{{ tip }}</div>
			</div>
		</div>
		<div class="row">
			<input v-model="captchar" maxlength="6" type="text" class="form-control" style="text-align: center">
		</div>


		<vAlert @tap="tap" :alertShow="alertShow" class="vAlert">
		</vAlert>
	</div>
</template>

<script>
	import axios from 'axios'
	import vAlert from './vAlert.vue'

	export default {
		components: {
			vAlert
		},
		data () {
			return {
				alertShow: false,
				captchar: '',
				captcharImg: 'http://mmvote.viphk.ngrok.org/api/getImg?userId=',
				index: 0,
				tip: '',
			}
		},
		beforeCreate () {
		},
		mounted () {
		},
		computed: {
			userId () {
				return this.$store.state.userId
			},
			captcharUrl () {
				return this.captcharImg + this.userId.id
			}
		},
		watch: {
			captchar () {
				if (this.captchar.length === 6) {
					axios.get('http://mmvote.viphk.ngrok.org/api/vote', {
						params: {
							userId: this.userId.id,
							captchar: this.captchar
						}
					})
						.then(res => {
							let data = JSON.parse(res.data)
							if (data.code === 0 && data.code === "0") {
								this.tip = '成功'
							}
							else if (data.code === -2 && data.code === "-2") {
								this.tip = data.msg + ' (已经投过票)'
							}
							else if (data.code === -3 && data.code === "-3") {
								this.tip = data.msg + ' (验证码不正确)'
							}
							else if (data.code === -9 && data.code === "-9") {
								this.tip = data.msg + ' (未登录)'
							}
							this.captchar = ''
							this.index++
						})
					/*this.captcharImg = ''
					setTimeout(() => {
						this.captcharImg = 'http://mmvote.viphk.ngrok.org/api/getImg?userId='
					}, 1000)*/
				}
			}
		},
		methods: {
			tap () {
			}
		}
	}
</script>

<style lang="stylus" scoped>
	.captcharBox{
		width:290px
		height: 70px
		margin 0 auto
		overflow hidden
		img{
			display: block
			margin -2px -3px
		}
	}
	.tip{
		height: 30px;
		line-height: 30px;
		margin-bottom: 10px;
	}
</style>



